@media only screen and (max-width: 600px) {

  body {
    font: 14px "Century Gothic", Futura, sans-serif;
    margin: 10%;
    background-color: aquamarine;
    box-sizing: border-box;
  }

}

@media only screen and (min-width: 600px) {
  body {
    font: 14px "Century Gothic", Futura, sans-serif;
    
    background-color: aquamarine;
    box-sizing: border-box;
    
    margin: 10% 30% 0% 30%;
  }
}

@media only screen and (min-width: 768px) {
  body {
    font: 14px "Century Gothic", Futura, sans-serif;
    
    background-color: aquamarine;
    box-sizing: border-box;
    margin: 5% 40% 0% 40%;
  }
}

  
  ol, ul {
    padding-left: 30px;
  }
  h3 {
    font: 40px "Century Gothic", Futura, sans-serif;
    position: relative;
    left: 10px;
    color:rgb(107, 189, 69);
    font-size: 50px;
    margin-bottom: 5px;
    font-weight: bold;
    
  }
  /* .board-row:after {
    clear: both;
    content: "";
    display: table;
  } */

  .board {
    display: flex;
    flex-direction: column;
  }

  .board-row {
    display: flex;
    flex-direction: row;
  }
  
  .status {
    margin-bottom: 40px;
  }
  
  .square {
    background: rgb(107, 189, 69);
    border: 2px solid rgb(27, 26, 26);
    border-radius: 10px;
    color: aqua;
    font-size: 50px;
    font-weight: bold;
    height: 100px;
    padding: 0;
    text-align: center;
    width: 100px;
    margin: 1px;
  }
  
